.aside_nav {
    grid-area: a;
    background-color: #e2e2e2;

    padding: 20px 10px;

    display: flex;
    flex-direction: column;
    gap: 30px;

    min-height: 700px;
}

.sector_nav {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.sector_nav a {
    width: fit-content;
}

.sector_nav a.activePage::after {
    content: '';
    display: block;

    width: 100%;
    height: 1px;
    background-color: #004c45;
}

.friends_online {
    justify-content: center;
    gap: 15px;
}

.friend_item {
    display: flex;
    flex-direction: row;
    gap: 10px;
    align-items: center;

    width: fit-content;

    color: rgb(75, 126, 38);
}

.friend_item img {
    width: 45px;
    height: 45px;

    border-radius: 50%;
}